import Vue from 'vue';
import Router from 'vue-router';
import Home from './Main.vue';
import Add from './MainAdd.vue';

Vue.use(Router)

export default new Router({
    routes: [{
        path: '*',
        redirect: '/'
    }, {
        path: '/',
        name: 'home',
        component: Home
    }, {
        path: '/add',
        name: 'add',
        component: Add
    }]
})