<template>
  <div id="app">
    <transition name="silde-fade">
      <router-view/>
    </transition>
  </div>
</template>

<script>
import Main from "./Main.vue";
import { dslArray, asideTree, model, rules } from "./mock.js";
let MSG_OPTIONS = {};

export default {
  provide() {
    return {
      APP: this
    };
  },
  data() {
    return {
      typeOfModel: "text",
      dsl: dslArray,
      asideTree,
      model,
      rules
    };
  },
  methods: {
    goTo(command, isConfirm) {
      if (command === "remove") {
        this.$confirm(`确实要删除当前的表单?`, "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            const loading = this.$loading();
            /* 发送this.currentModel到后台处理删除请求 */
            console.log(this.currentModel);
            setTimeout(() => {
              this.afterRemove(loading);
            }, 1000 * 2);
          })
          .catch(error => {
            console.log("cancel", error);
          });
      } else {
        if (isConfirm) {
          this.$confirm(`确实离开?`, "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          })
            .then(() => {
              this.$router.push({
                name: command
              });
            })
            .catch(error => {
              console.log("cancel", error);
            });
        } else {
          this.$router.push({
            name: command
          });
        }
      }
    },
    async save({ loading } = {}) {
      /* loading框 */
      loading = loading || this.$loading();
      try {
        let doc = this.model;
        /* save */
        debugger;
        let response = await this.$post("/proxy/save", {
          coll: "fnbpc_hyjc" /* collection */,
          doc /* document */
        });
        if (response.isSuccess) {
          /* 刷新列表 */
          MSG_OPTIONS = {
            type: "success",
            message: "保存成功"
          };
          /* 保存成功，更新当前展示的 */
          this.goTo("home");
        } else {
          throw new Error(response.data);
        }
      } catch (error) {
        MSG_OPTIONS = {
          type: "error",
          message: error.message
        };
      } finally {
        loading.close();
        this.$message({ showClose: true, ...MSG_OPTIONS });
      }
    },
    handleNodeClick(data, node, vm) {
      /* 从data中获取信息，查找model的信息 */
      this.model = { ...data };
      const STRATEGE = {
        combobox: () => {
          let dataString = this.model.qzfw;
          let arr1 = dataString.split(";");
          let comboboxData = arr1.map(dataString2 => {
            let arr2 = dataString2.split(",");
            return {
              text: arr2[1],
              value: arr2[0]
            };
          });
          this.dslArray[0].combobox[0][1].items[0].data = comboboxData;
          this.dslArray = JSON.parse(JSON.stringify(this.dslArray));
          console.log("comboboxData", comboboxData);
        }
      };
      STRATEGE[this.model.lx] && STRATEGE[this.model.lx]();
    }
  },
  computed: {
    treeOptions() {
      let treeOptions = {
        props: {
          children: "children",
          label: "text"
        },
        showCheckbok: false
      };
      treeOptions.data = this.asideTree;
      return treeOptions;
    }
  },
  watch: {
    model(model) {
      console.log("model", model);
    }
  },
  components: {
    CMain: Main
  }
};
</script>

<style src="@c/styles/app.scss" lang="scss">
</style>
<style src="@c/styles/hqbj.scss" lang="scss">
</style>


